@import url(https://fonts.googleapis.com/css2?family=Raleway:wght@200&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  text-decoration: none;
  font-family: 'Raleway', sans-serif;
}

img {
  margin-top: -15%;
  max-width: 55%;
  border-radius: 20%;
  height: auto;
}

footer {
  border: solid rgba(250, 249, 249, 0.822) 3px;
  width: 55%;
  align-self: center
}

.home {
  background: url(/resume/static/media/hills.f34f0e19.jpeg) no-repeat center center fixed;
  background-size: cover;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100vh;
  width: 100vw;
}

.profile {
  display: flex;
  background-color: rgba(250, 249, 249, 0.822);
  margin-bottom: 3rem;
  height: 70vh;
  border-radius: 15px;
  width: 90vw;
  text-align: center;
  font-size: 1.5rem;
}

.left {
  display: flex;
  flex-direction: column;
  color: rgb(56, 105, 64);
  font-size: 0.85vw;
  background-color: rgba(203, 240, 182, 0.507);
  border-radius: 15px;
  width: 20%;
}

.right {
  display: flex;
  width: 80%;
  justify-content: center;
  align-items: center;
  font-size: 5rem;
}

.title {
  background: rgba(203, 240, 182, 0.507);
  padding: 0 0.5rem 0.5rem 0.5rem;
  width: -webkit-max-content;
  width: max-content;
  align-self: center;
  border-radius: 15%;
}

@media (max-width: 1400px) {
  img {
    max-width: 65%;
  }

  .left {
    font-size: 0.75vw;
  }
}

@media (max-width: 415px) {
  img {
    max-width: 85%;
  }

  .left {
    font-size: 1.6vw;
    width: 30%
  }
}
